import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import {db} from './firebase'
import { createPinia } from 'pinia';



/* import the fontawesome core */
import { library } from '@fortawesome/fontawesome-svg-core'

/* import font awesome icon component */
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

/* import specific icons */
import { faArrowLeft, faBars, faBook, faBookOpen, faEllipsisV, faGear, faHouse, faRightFromBracket, faSquarePen, faUserSecret } from '@fortawesome/free-solid-svg-icons'
import { faImage, faPenToSquare, faTrashCan, faUser } from '@fortawesome/free-regular-svg-icons'
import './registerServiceWorker'

/* add icons to the library */
library.add(faUserSecret,faHouse,faPenToSquare,faUser,faSquarePen,faBook,faImage,faEllipsisV,
    faTrashCan,faBookOpen,faGear,faBars,faArrowLeft,faRightFromBracket)
const pinia = createPinia()
const app = createApp(App)
app.config.devtools = true;
app.config.productionTip = false;
db;
app.use(pinia).component('font-awesome-icon', FontAwesomeIcon).use(router).mount('#app');
