<template>
    <div v-if="isLoggedIn">
  <nav>
    <router-link class="hamburger" to="#"  @click="openSidebar"><font-awesome-icon :icon="['fas', 'bars']" /></router-link>
    <router-link class="nav-title-mobile" to="/"><span class="adi">A</span>dah's <span class="adi">Di</span>ary</router-link>
    <router-link class="nav-title" to="/"><span class="adi">A</span>dah's <span class="adi">Di</span>ary</router-link>
    <router-link class="links" to="/"><font-awesome-icon :icon="['fas', 'book']" size="lg" /></router-link>
    <router-link class="links" to="/diary"><font-awesome-icon :icon="['far', 'pen-to-square']" size="lg" /></router-link>
    <router-link class="links" to="/profile"><font-awesome-icon :icon="['far', 'user']" size="lg" /></router-link>
    <router-link class="settings" to="#"  @click="logout"><font-awesome-icon :icon="['fas', 'right-from-bracket']" /></router-link>
  </nav>
  <router-view v-if="isLoggedIn"/>
</div>
<div v-else>
  <router-view/>
</div>
<div>
      <SideBar @closeSidebar="closeSidebar" :sideBarWidth="sideBarWidth" :left="left"/>
    </div>
</template>

<script setup>
import { useRouter } from 'vue-router'
import {ref, onMounted} from 'vue'
import {getAuth, onAuthStateChanged,signOut} from 'firebase/auth'
import SideBar from './components/SideBar.vue'


import { userInformationStore } from '@/store/user.js'

const store = userInformationStore()

    const router = useRouter()
    const isLoggedIn = ref(false)
    const sideBarWidth = ref('0px')
    const left = ref('-50%')

    let auth
    const logout = ()=>{
      signOut(auth).then(()=>{
        isLoggedIn.value = false
        localStorage.removeItem('userId');
        router.push('/login')
      })
    }

    const openSidebar = ()=>{
      sideBarWidth.value = '250px';
      left.value = '0'
    }
    const closeSidebar = ()=>{
      sideBarWidth.value = '0'
      left.value = '-50%'
    }

    onMounted(()=>{
      auth = getAuth();
      onAuthStateChanged(auth,(user)=>{
        if(user){
          isLoggedIn.value = true
          localStorage.setItem("userId",getAuth().currentUser.uid)
        }else{
          isLoggedIn.value = false
        }
      });
    })
</script>

